 .A0 {
     height: 100%;
     width: 100%;
     display: flex;
     flex-direction: column;
     justify-content: space-between;
 }

 .A1 {
     display: flex;
     flex-direction: column;
     width: 50%;
     margin: 0 auto;
     margin-right: 6rem;
 }

 .A2 {
     font-size: 2.5rem;
     letter-spacing: 0.2rem;
     font-style: italic;
     font-weight: bolder;
     text-decoration: none;
     width: fit-content;
     z-index: 1;
 }

 .A2 a {
     text-decoration: none;
 }

 .A3 {
     color: #070707;
     cursor: pointer;
 }

 .A4 {
     -webkit-text-stroke: 1px #070707 ;
     color: #e81038;
     cursor: pointer;
 }

 .A5 {
     position: fixed;
     top: 1.6rem;
     left: 16rem;
     z-index: 0;
 }

 .A5 {
    position: relative;
    margin: -8rem 0px 0px calc((100% - 489px) * 0);
    left: 258px;
    grid-area: 1 / 1 / 2 / 2;
    justify-self: start;
    align-self: start;
    height: 100px;
    width: 80px;
 }

 .A5 img {
    height: 80px;
    width: 100px;
 }

 .A5:before {
    content: "";
    position: absolute;
    bottom: -26px;
    width: 165px;
    height: 26px;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;

    left: -1rem;
    background-position: 0 0;
    top: 5rem;
 }

 .A5:after {
    content: "";
    position: absolute;
    bottom: -26px;
    width: 165px;
    height: 26px;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;

    right: -2rem;
    background-position: 100% 0;
    top: 5rem;
 }